body {
  background-color: #282828;
  height: 20000px;
}

.container {
  background-color: #282828;
}

#chart {
  background-color: #282828;
}

.circle {
  stroke: #000;
  stroke-width: 0px;
}

.line {
  fill: none;
  stroke: #ab1ab5;
  stroke-width: 2px;
}

.figureLine {
  fill: none;
}

.deltaline {
}

.domain {
  stroke: #888;
  stroke-width: 1px;
}

.yAxis > .domain {
  stroke: #888;
  stroke-width: 0px;
}

.tick {
  stroke: #fff;
  color: #fff;
  fill: #fff;
  stroke-width: 1px;
}

.indicator {
  font-family: Verdana;
  fill: #ddd;
  font-size: 0.5em;
}

.now {
  font-family: Verdana;
  fill: #fa6900;
  font-size: 0.8em;
}

.dataLabel {
  font-family: "Trebuchet MS", Trebuchet, Verdana, sans-serif;
  fill: #ddd;
}

.anomaly {
  font-family: Verdana;
  fill: #fa6900;
}

.circle-anomaly {
  stroke: #000;
  stroke-width: 0px;
  fill: #fa6900;
  fill-opacity: 1;
}

.data-time-indicator {
  stroke: #000;
  stroke-width: 0px;
  fill: #ab1ab5;
  fill-opacity: 0.5;
}

.current-time-indicator {
  stroke: #000;
  stroke-width: 0px;
  fill: #fa6900;
  fill-opacity: 0.5;
}
